::selection {
  background-color: rgba(255, 69, 0, 0.8); }

.page {
  min-height: 100%;
  font-size: 10px;
  font-family: "Oswald Regular", "Arial", sans-serif;
  line-height: 27px; }

.body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #000000; }

.main-title {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  color: #111111;
  pointer-events: none;
  margin: 0 auto 28px;
  padding: 14px 20px 0;
  border-top: 1px solid #e6e6e6; }
  @media (min-width: 768px) {
    .main-title {
      font-size: 60px;
      line-height: 60px;
      margin-bottom: 163px;
      padding: 30px 30px 0;
      border-top: none; } }
  @media (min-width: 1200px) {
    .main-title {
      padding: 60px 110px 0;
      margin-bottom: 165px; } }

@media (min-width: 768px) {
  .main-title--form {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 55px;
    padding: 30px 70px 0;
    border-top: none; } }

@media (min-width: 1200px) {
  .main-title--form {
    padding: 60px 110px 0;
    margin-bottom: 73px; } }

.hidden {
  display: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (min-width: 768px) {
  .mobile-visible {
    display: none; } }

.mobile-only--hidden {
  display: none; }
  @media (min-width: 767px) {
    .mobile-only--hidden {
      display: initial; } }

@media (max-width: 767px), (min-width: 1199px) {
  .tablet-visible {
    display: none; } }

.desktop-visible {
  display: none; }
  @media (min-width: 1200px) {
    .desktop-visible {
      display: initial; } }

.animation-scale {
  animation: animation-scale var(--animation-duration, 1s) ease; }

@keyframes animation-scale {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }

.animation-position {
  position: relative;
  animation: animation-position 1s ease; }

@media (min-width: 320px) {
  @keyframes animation-position {
    0% {
      left: 50%; }
    100% {
      left: 0; } } }

@media (min-width: 768px) {
  @keyframes animation-position {
    0% {
      left: 50%; }
    100% {
      left: 30px; } } }

@media (min-width: 1200px) {
  @keyframes animation-position {
    0% {
      left: 50%; }
    100% {
      left: 0; } } }

/* Blocks */
.header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 5em;
  box-sizing: border-box;
  background-color: #222222; }

.header__title {
  display: block;
  width: fit-content;
  color: #ffffff;
  font-family: "Oswald Medium", "Arial", sans-serif;
  font-size: 3em; }
